@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;800&family=Mukta:wght@200;400;800&family=Roboto+Condensed&display=swap');

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  scroll-behavior: smooth;
  color: #e4e4e4;
  background: #0b1b2c url(../public/images/bg.jpg);
  background-size: cover;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #e4e4e4;
}

a:hover {
  text-decoration: none;
  color: #fff;
}

.container {
  display: flex;
  flex-flow: column;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: flex-start;
}

header {
  display: flex;
  flex-flow: column;
  justify-content: center;
  position: absolute;
  top: 0;
  width: 100%;
  padding: 2%;
  background: rgba(41, 48, 67, 0.6);
}

.addContact {
  position: relative;
  display: flex;
  width: 45%;
  align-self: center;
  background: rgba(41, 48, 67, 0.6);
  border-radius: 10px;
  justify-content: flex-start;
  padding: 2%;
  margin-top: 10%;
  flex-direction: column;
}

.addContact h4 {
  font-weight: 600;
  margin: 3%;
  padding-bottom: 20px;
}

.addContact input {
  background: rgba(41, 48, 67, 0.6) url(../public/images/search.svg) right no-repeat;
  border: none;
  color: #4f525f;
  font-size: 1.5vw;
  align-self: center;
  width: 95%;
  padding: 10px;
}

.divider {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  padding-bottom: 20px;
}

label {
  width: 50%;
  display: inline-flex;
  margin: 2%;
}

.divider input {
  margin: 2%;
  font-size: 1.5vw;
  border: none;
  background: rgba(41, 48, 67, 0.6);
  padding: 10px;
  border-radius: 5px;
  color: #4f525f;
  border-radius: 10px;
}

.addContact button {
  border: none;
  background: rgba(152, 93, 125, 0.8);
  font-weight: 800;
  color: #e4e4e4;
  width: 70%;
  padding: 2%;
  border-radius: 10px;
  align-self: center;
  cursor: pointer;
  margin-top: 5%;
}

.addContact button:hover {
  background: #7c4965;
  color: #ad8a9d;
}

.contactList {
  display: flex;
  flex-flow: row;
  background: rgba(41, 48, 67, 0.6);
  color: #666;
  margin: 5px;
  justify-content: space-between;
  padding: 10px;
}

.contactData {
  display: flex;
  flex-flow: column;
  width: 80%;
  background: url(../public/images/user.svg) left top no-repeat;
  background-size: 15%;
  padding-left: 15%;
  fill: #fff;
}

.contactData :first-child {
  color: #e4e4e4
}

.contactList img {
  position: relative;
  object-fit: contain;
  width: 10%;
  fill: #fff;
  cursor: pointer;
}

.btnSec {
  display: flex;
  width: 90%;
  justify-content: center;
}

.contactDetails {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
}

.contactDetails img {
  object-fit: contain;
  width: 60%;
  align-self: center;
  border-radius: 50%;
}

.contactDetails span {
  margin: 10px;
  display: flex;
  width: 100%;
  font-size: 2vw;
  justify-content: center;
  font-weight: 600;
}

.divider select {
  width: 95%;
  background: rgba(41, 48, 67, 0.6);
  font-size: 1.5vw;
  border: none;
  padding: 8px;
  border-radius: 10px;
  align-self: center;
  color: #4f525f;
}

.divider option {
  background: rgba(41, 48, 67, 0.6);
  color: #666;
}

.closeBtn {
  position: absolute;
  width: 10%;
  height: 10%;
  top: 0;
  right: -24%;
  cursor: pointer;
}

.model {
  display: flex;
  position: absolute;
  background: rgba(41, 48, 67, 0.8);
  width: 100%;
  height: 100%;
  justify-content: center;
  z-index: 10;
}

.modelContainer {
  align-self: center;
  width: 40%;
  height: 30%;
  background: rgba(86, 96, 126, 0.6);
  padding: 5%;
  border-radius: 10px;
}

.redBtn {
  border: none;
  background: rgba(152, 93, 125, 0.8);
  font-weight: 800;
  color: #e4e4e4;
  width: 70%;
  padding: 2%;
  border-radius: 10px;
  align-self: center;
  cursor: pointer;
  margin-top: 5%;
  margin: 2%;
}

.margin20 {
  margin-top: 20%;
}

.blueBtn {
  border: none;
  background: rgba(94, 93, 152, 0.8);
  font-weight: 800;
  color: #e4e4e4;
  width: 70%;
  padding: 2%;
  border-radius: 10px;
  align-self: center;
  cursor: pointer;
  margin-top: 5%;
  margin: 2%;
}

.avatarImg {
  display: flex;
  align-self: center;
  width: 60%;
  justify-content: center;
  object-fit: cover;
}

.avatarImg svg {
  width: 100%;
  height: 100%;
}

.active {
  background: rgba(94, 93, 152, 0.8);
  font-weight: bold;
}